body {
  font-family: 'Inter', 'Segoe UI', 'Arial', sans-serif;
  background: #f4f6fa;
  margin: 0;
}

.flashcard-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e0e7ff 0%, #f0abfc 100%);
  padding: 2rem;
}

.flashcard-box {
  background: #fff;
  border-radius: 1.5rem;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.15);
  padding: 2.5rem 2rem;
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flashcard-title {
  font-size: 2.5rem;
  font-weight: 800;
  color: #3b0764;
  margin-bottom: 1.5rem;
  text-align: center;
}

.flashcard-chapter {
  color: #7c3aed;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

.flashcard-count {
  color: #64748b;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.flashcard-card {
  background: #f0f9ff;
  border-radius: 1rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  width: 100%;
  min-height: 180px;
  box-shadow: 0 2px 8px 0 rgba(59, 7, 100, 0.07);
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}
.flashcard-card:hover {
  transform: scale(1.03);
  box-shadow: 0 4px 16px 0 rgba(59, 7, 100, 0.13);
}
.flashcard-question {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 1rem;
  text-align: center;
}
.flashcard-options {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
.flashcard-option {
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
  background: #fff;
  color: #334155;
  font-size: 1rem;
  transition: background 0.2s, color 0.2s;
}
.flashcard-option.correct {
  background: #bbf7d0;
  color: #166534;
  font-weight: 700;
  box-shadow: 0 0 0 2px #22d3ee33;
}
.flashcard-answer {
  margin-top: 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  color: #059669;
}
.flashcard-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}
.flashcard-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(90deg, #7c3aed 0%, #38bdf8 100%);
  color: #fff;
  font-weight: 600;
  border: none;
  border-radius: 999px;
  box-shadow: 0 2px 8px 0 rgba(59, 7, 100, 0.10);
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}
.flashcard-btn:hover {
  background: linear-gradient(90deg, #38bdf8 0%, #7c3aed 100%);
  transform: scale(1.05);
}
